import { Component } from '@angular/core';
import { NavController, NavParams, IonicPage  } from 'ionic-angular';
import { Values } from '../../../providers/service/values';
import { Service } from '../../../providers/service/service';
//import { ReturnProduct } from '../return-product/return-product'
import { Functions } from '../../../providers/service/functions';

@IonicPage()
@Component({
  templateUrl: 'order-details.html',
})
export class OrderDetails {

  id: any;
  href:any;
  status:any;
  orderDetails: any;
  
  constructor(public nav: NavController, public service: Service, params: NavParams, public functions: Functions, public values: Values) {

    this.id = params.data;
    
  	this.service.getOrder(this.id)
    .then((results) => this.orderDetails = results);  
  }

  
  reorder(href){
    this.service.addToCart(href)
    .then((results) => this.handleCart(results));
  }

  handleCart(results){
    if(results.success){
      this.functions.showAlert(this.values.lan.text_success, results.success);
    }
  }

  
  returnProduct(href){
    this.nav.push('ReturnProduct', href); 
  }
   

  encodeUrl(href) {
    return href.replace(/&amp;/g, '&');
  }

}


